import React, { Component } from "react";
import MainContext from "../context";
import Employee from "./Employee";
import EmployeeForm from './EmployeeForm'


class RegisterForm extends Component {
  constructor(props) {
    super(props);
    this.state = { loading: false, authenticated: true };
  }
  
  render() {
    const { loading, authenticated } = this.state;
    return (
      <MainContext.Consumer>
        {({ employees, toggleActiveStatus, handleEmployeeForm }) => (
          <React.Fragment>
              <div className="container">
                <div className="row justify-content-center">
                  <div className="col-10">
                  <h3 className="text-center text-dark mt-3">
                List of Employees <br />
                </h3>
                <table className="table table-bordered table-stripped mt-4">
                  <thead className="">
                    <tr>
                      <td>
                        <b>Όνομα</b>
                      </td>
                      <td>
                        <b>Επίθετο</b>
                      </td>
                      <td>
                        <b>Department</b>
                      </td>
                      <td>
                        <b>Company</b>
                      </td>
                      <td>
                        <b>Employment Status</b>
                      </td>
                    </tr>
                  </thead>
                  <tbody>
                    {employees.map((emp, idx) =>  (
                      <Employee
                        toggleActiveStatus={toggleActiveStatus}
                        key={idx + emp.firstName}
                        employee={emp}
                      />
                    ) )}
                  </tbody>
                </table>
                <br />
                  </div>
                </div>
              </div>
              <div className="row mt4 justify-content-center mb-5">
                <div className="col-sm-8 col-md-5">
                        <h4>Add New Employee</h4>
                        <hr/>
                        <EmployeeForm handleSubmit={handleEmployeeForm} />
                </div>
              </div>
          </React.Fragment>
        )}
      </MainContext.Consumer>
    );
  }
}

export default RegisterForm;



